@import url(https://fonts.googleapis.com/css2?family=Neonderthaw&display=swap);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #003;
  padding-top: 50px;
  color: white;
  text-align: center;
}


.Cell {
  height: 100px;
  width: 100px;
  background-color: darkgray;
}

.Cell-lit {
  background-color: white;
}
body {
  box-sizing: border-box;
}

.Board {
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 0 50px #fff, 
              -10px 0 50px #f0f, 
              10px 0 50px #0ff;
}

table tr:first-child td:first-child {
  border-radius: 10px 0 0 0 !important;
}
table tr:first-child td:last-child {
  border-radius: 0 10px 0 0 !important;
}
table tr:last-child td:last-child {
  border-radius: 0 0 10px  0 !important;
}
table tr:last-child td:first-child {
  border-radius: 0 0 0 10px !important;
}


p {
  margin: 0;
  padding: 0;

}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.winner .neon-blue .neon-orange .title {
  font-size: 200px;
  height: 100vh;
}


.neon-orange{
  font-family: 'Neonderthaw', cursive;
  color:#fb4264;
  font-size: 80px;
  line-height: 9vw;
  text-shadow: 0 0 3vw #f40a35;
  -webkit-animation: neon-orange 1s ease infinite;
          animation: neon-orange 1s ease infinite;
  -moz-animation: neon-orange 1s ease infinite;
  -webkit-animaton: neon-orange 1s ease infinite;
}

.neon-blue {
  font-family: 'Neonderthaw', cursive;
  color:#426dfb;
  font-size: 80px;
  line-height: 9vw;
  text-shadow: 0 0 3vw #2356ff;
  -webkit-animation: neon-blue 2s linear infinite;
          animation: neon-blue 2s linear infinite;;
  -moz-animation: neon-blue 2s linear infinite;;
  -webkit-animaton: neon-blue 2s linear infinite;;
  -o-animation: neon-blue 2s linear infinite;
}


@-webkit-keyframes neon-orange {
  0%,
  100% {
    text-shadow: 0 0 1wv  #fa1c16, 0 0 3vw #fa1c16, 0 0 10wv #fa1c16, 0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b, 0 0 0.2vw #800e0b,0 0 0.1vw #40340a;
    color: #806914;
  }
}


@keyframes neon-orange {
  0%,
  100% {
    text-shadow: 0 0 1wv  #fa1c16, 0 0 3vw #fa1c16, 0 0 10wv #fa1c16, 0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b, 0 0 0.2vw #800e0b,0 0 0.1vw #40340a;
    color: #806914;
  }
}

@-webkit-keyframes neon-blue {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041FF, 0 0 3vw #1041FF, 0 0 10vw #1041FF, 0 0 10vw #1041FF, 0 0 .4vw #8BFDFE, .5vw .5vw .1vw #147280;
    color: #28D7FE;
  }
  50% {
    text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180, .5vw .5vw .1vw #0A3940;
    color: #146C80;
  }
}

@keyframes neon-blue {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041FF, 0 0 3vw #1041FF, 0 0 10vw #1041FF, 0 0 10vw #1041FF, 0 0 .4vw #8BFDFE, .5vw .5vw .1vw #147280;
    color: #28D7FE;
  }
  50% {
    text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180, .5vw .5vw .1vw #0A3940;
    color: #146C80;
  }
}


.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: auto;
}

